function enviar_orcamento(btn, loading, nome, telefone, email, tipo, aviso){
  if(document.getElementById(nome).value !== '' && document.getElementById(telefone).value !== '' && document.getElementById(email).value !=='' && document.getElementById(tipo).value !== ''){
    document.getElementById(btn).style.display = 'none';
    document.getElementById(loading).style.display = '';
    document.getElementById(aviso).innerText = '';

    let form_up = new FormData();
    form_up.append("nome", document.getElementById(nome).value);
    form_up.append("telefone", document.getElementById(telefone).value);
    form_up.append("email", document.getElementById(email).value);
    form_up.append("tipo", document.getElementById(tipo).value);

    fetch(api + "controller.php?acao=orcamento",{
      body: form_up,    
      method: "POST",
    })
      .then((response) => response.json())
      .then((response) => {
        if(response.status_service === 'ok'){
          document.getElementById(btn).style.display = '';
          document.getElementById(loading).style.display = 'none';
          document.getElementById(aviso).innerText = 'Recebemos sua solicita√ß√£o e em breve nossa equipe entrar√° em contato. üòä';
        }else{
          document.getElementById(btn).style.display = '';
          document.getElementById(loading).style.display = 'none';
          document.getElementById(aviso).innerText = 'Houve um erro, entre em contato conosco via WhatsApp pelo n√∫mero (11) 99008-3256. üòä';

        }
      })
      .catch((rejected) => {
        document.getElementById(btn).style.display = '';
        document.getElementById(loading).style.display = 'none';
        document.getElementById(aviso).innerText = 'Houve um erro, entre em contato conosco via WhatsApp pelo n√∫mero (11) 99008-3256. üòä';
        console.log(rejected);
      }); 
  }else{
    document.getElementById(btn).style.display = '';
    document.getElementById(loading).style.display = 'none';
    document.getElementById(aviso).innerText = 'Preencha todos os campos para continuar ou entre em contato conosco via WhatsApp pelo n√∫mero (11) 99008-3256. üòä';
  }
}

